.form-item--checkbox {
  position: relative;
}
.form-item--checkbox input {
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip: rect(0, 0, 0, 0);
  position: absolute;
}
.form-item--checkbox label {
  display: flex;
}
.form-item--checkbox label:after {
  content: "\ea03";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-family: "icons";
}
.form-item--checkbox label:before {
  content: " ";
  display: inline-block;
  width: 2rem;
  height: 2rem;
  border: 0.2rem solid #D1DFE3;
  margin-right: 1.5rem;
  transition: border-color 0.2s;
  margin-top: 0.3rem;
}
.form-item--checkbox label:after {
  position: absolute;
  left: 1.6rem;
  top: 0.8rem;
  font-size: 1.4rem;
  display: none;
  color: #004152;
}
.form-item--checkbox label:hover, .form-item--checkbox label:focus {
  text-decoration: underline;
}
.form-item--checkbox label:hover:before, .form-item--checkbox label:focus:before {
  border-color: #004152;
}
.form-item--checkbox input:checked + label:before {
  border-color: #25b34b;
  box-shadow: 0.3rem 0.3rem 0 rgba(37, 179, 75, 0.3);
  background: #fff url("../images/ico-check.svg") center center no-repeat;
  background-size: 1.2rem auto;
}
.form-item--checkbox input:checked + label:after {
  display: block;
}

