  .stepnav {
    padding-right: 3rem;
    padding-left: 3rem;
  }
}
@media (min-width: 560px) {
  .stepnav {
    margin-left: -3rem;
    margin-right: -3rem;
  }
}
@media (min-width: 780px) {
  .stepnav {
    padding-bottom: 12rem;
    margin-bottom: -7rem;
  }
}
