  .stepnav {
    padding-top: 2rem;
    padding-bottom: 16rem;
    margin-bottom: -10rem;
  }
}

.stepnav__bg {
  display: flex;
  width: 45%;
  max-width: 43rem;
  margin: 0 auto;
  z-index: 1;
  position: relative;
}
.stepnav__bg > div {
  overflow: hidden;
  height: 4rem;
  width: 50%;
}
.stepnav__bg .right {
  transform: scaleX(-1);
}
.stepnav__bg svg {
  fill: rgba(255, 255, 255, 0.6);
}

.stepnav__items {
  padding: 0;
  margin: 1rem auto 0;
  display: flex;
  width: 100%;
  justify-content: space-between;
  counter-reset: stepnav;
  max-width: 1000px;
  position: relative;
  z-index: -1;
}
@media (min-width: 780px) {
  .stepnav__items {
    height: 11rem;
  }
  .stepnav__items:before, .stepnav__items:after {
    margin-top: -13rem;
  }
  .stepnav__items:before {
    left: 29%;
  }
  .stepnav__items:after {
    right: 29%;
  }
}

.stepnav__step {
  list-style: none;
  flex: 1;
  text-align: center;
  position: relative;
  display: block;
  counter-increment: stepnav;
}
.stepnav__step .stepnav__linktext:before {
  content: counter(stepnav) ". ";
}
.stepnav__step:before {
  content: " ";
  position: absolute;
  display: block;
  height: 0.5rem;
  top: 2.4rem;
  background: #f2ebe4;
  width: 100%;
  left: 50%;
  speak: none;
}
.stepnav__step:first-child:before, .stepnav__step:nth-last-child(2):before {
  background: rgba(255, 255, 255, 0.6);
}
.stepnav__step:first-child:before {
  left: 50%;
}
.stepnav__step:last-child:before {
  display: none;
}
@media (min-width: 780px) {
  .stepnav__step:before {
    height: 0.7rem;
    top: 3.9rem;
  }
}

.stepnav__link {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  z-index: 5;
  text-decoration: none;
  padding-top: 1.2rem;
}
.stepnav__link:before {
  content: " ";
  position: absolute;
  width: 5.5rem;
  height: 5.5rem;
  top: 0;
  left: 50%;
  margin-left: -2.75rem;
  background: #f99d1c;
  z-index: -1;
  border-radius: 50%;
  transition: background 0.2s, transform 0.2s;
  outline: 1rem solid #25b34b;
}
.stepnav__link span {
  position: relative;
  z-index: 1;
}
.stepnav__link:hover:before, .stepnav__link:focus:before, .stepnav__link.is-active:before {
  background: #f99d1c;
  transform: scale(1.05);
}
.stepnav__link:hover .icon--stepnav, .stepnav__link:focus .icon--stepnav, .stepnav__link.is-active .icon--stepnav {
  transform: scale(1.05);
}
.stepnav__link:hover .stepnav__linktext, .stepnav__link:focus .stepnav__linktext, .stepnav__link.is-active .stepnav__linktext {
  text-decoration: underline;
}
@media (min-width: 780px) {
  .stepnav__link {
    padding-top: 1.4rem;
  }
  .stepnav__link:before {
    width: 8.4rem;
    height: 8.4rem;
    margin-left: -4.2rem;
    outline: 1.5rem solid #25b34b;
  }
}

.icon--stepnav {
  display: inline-block;
  width: 3rem;
  height: 3.5rem;
  transition: background-color 0.2s, transform 0.2s;
  fill: #fff;
}
.is-active .icon--stepnav {
